import { Component, OnInit, HostListener } from '@angular/core';
import { PoHeader } from '../_entities/po-header';
import { InvoiceUtilsService } from '../_services/invoice-utils.service';
import { Page } from '../_entities/page';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss']
})
export class DetailsComponent implements OnInit {

  invHeaderItems : Array<PoHeader>;
  page = new Page();
  pages = new Array();
  totalPages = 3;
  activeCard = "OPEN";
  umModal: BsModalRef;
  selInvoiceHeader : PoHeader;
  pDocNum : string = '';
  vCode : string = '';
  filterApplied : boolean = false;
  po = new Object();

  constructor(private invoiceUtil : InvoiceUtilsService, 
    private toastr: ToastrService) { }

  ngOnInit() {
    this.getPurchaseOrders(this.activeCard);
    this.getMetadata();
  }
getPurchaseOrders(activeCard : string) : void {
    this.activeCard = activeCard;
    //this.clearFilter();
    this.getPOs(false, 0, activeCard);    
  }
 

  getPOs(isRefresh : boolean, pNo : number, status : string, isNext = false, isPrev = false) : void {
    this.invoiceUtil.getPurchaseOrders(this.pDocNum, this.vCode, pNo, status)
    .subscribe(data => {      
      this.invHeaderItems = data.content;
      this.page.totalElements = data.totalElements;
        this.page.number = data.number;
        this.page.totalPages = data.totalPages;
        if (this.pages.length == 0) {
          if (this.page.totalPages <= this.totalPages)
            this.pages = this.array(this.page.totalPages);
          else
            this.pages = this.array(this.totalPages);
        }

        if (isNext && (this.page.number - 1 == this.pages[this.pages.length - 1])) {
          this.pages.splice(0, 1);
          this.pages.push(this.page.number);
        }
        if (isPrev && (this.page.number + 1 == this.pages[0])) {
          this.pages.splice(this.pages.length - 1, 1);
          this.pages.splice(0, 0, this.page.number);
        }
    }, error => {
      this.toastr.error(error.error.message);
    });
  }

  setPage(pageNo: number, isNext: boolean, isPrev: boolean): void {
    this.page.number = pageNo;
    this.getPOs(false, pageNo,this.activeCard,isNext, isPrev);
  }

  array(n: number): number[] {
    return Array.from(Array(n).keys());
  }
  
  getMetadata() {
    this.invoiceUtil.poMetadata()
    .subscribe(data => {
        this.po = data;
    })       
  }

}
