import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Observable} from 'rxjs';
import { PagedData } from '../_entities/paged-data';
import { PoHeader } from '../_entities/po-header';

@Injectable({
  providedIn: 'root'
})
export class InvoiceUtilsService {

  constructor(private http: HttpClient) { }

  getPurchaseOrders(pDocNum : string, vCode : string, pNo = 0, status = 'OPEN', pSize = 5) : Observable<PagedData<PoHeader>> {
    var qStr = "pDocNum="+pDocNum+"&vCode="+vCode+"&pNo="+pNo+"&pSize="+pSize+"&status="+status;
    return this.http.get<PagedData<PoHeader>>("http://joshikpf1ha1xv.opentext.net:8908/sso/api/poData");
  } 

  poMetadata() : Observable<Map<string,number>> {
    return this.http.get<Map<string,number>>("http://joshikpf1ha1xv.opentext.net:8908/sso/api/metadata");
  }
}
